import React, { Component } from 'react'
import { Section } from './general/style/common';
import { H2, TextContent} from "./general/style/style";


export default class GadgetDescription extends Component {
  render() {
    return (
      <Section padding="0 16px">  
        { this.props.sectionTitle && <H2 padding="0 0 10px 0" id={this.props.sectionId}>{this.props.sectionTitle}</H2>}
        <TextContent  dangerouslySetInnerHTML={{__html:this.props.description}}>
        </TextContent>
      </Section>
    )
  }
}
